.headerWrapper{transition: all 0.3s ease-in-out; background: #fff; position: fixed; top: 0px; left: 0px; width: 100%; z-index: 1000;}
.headerWrapper.fixed{ top: -66px;  }
 .after-header{margin-top: 135px;}
 header .container-fluid {height: 45px;}
 header .container-fluid .logo{height: 100%; }
 header .container-fluid .logo img{object-fit: fill; width: 100%; height: 100%;}

header{width: 100%; height:auto; margin: auto; padding: 10px 0;}

header .headerSearch{width: 100%; height:45px; border:1px solid #1c61e7; position: relative;}
header .headerSearch .search::before{
    content: ''; 
    width: 1px; 
    height: 30px; 
    background: rgba(0, 0, 0,0.1);
    position: absolute; 
    top: 8px;
}

header .headerSearch .search{width: 66%; position: relative;}
header .headerSearch .search input{ 
    margin-left:16px;
    width: 111%; 
    height: 42px; 
    border: 0px; 
    outline: none !important; 
    font-size: 16px; 
    max-width: 100%;
}
header .headerSearch .search .searchIcon {
    position: absolute;
    top: 10px;
    right: -11px;
    opacity: 0.5;
}
header .countryWrapper  {
    width: 100%;
    height: 45px;
    border: 1px solid rgba(0, 0, 0,0.1);
    margin-right: 8px;

}
header .countryWrapper  .select-search-wrapper{width: 140px;height: 45px;}
header .countryWrapper  .select-search-wrapper .arrow-icon{position: absolute; top: 14px;right: 3px;width: 18%;}

header .headerTabs{padding-left: 24px; display: flex;}
header .headerTabs .list-inline-item {display: flex; align-items: center; height: 54px;}
header .headerTabs .account{position: relative;}
header .headerTabs .list-inline-item .account-info{    
    width: 200px;
    position: absolute;
    top: 93%;
    right: -5px;
    background: #fff;
    padding: 8px;    
    border: 1px solid #ececec;
    border-radius: 12px;
    height: auto;
    z-index: 999;
}
header .headerTabs .list-inline-item .account-info li{
    width: 100% !important;
    list-style: none;
    margin: 0px !important;
}
header .headerTabs .list-inline-item .account-info li button{
    width: 100% !important;
    text-align: left !important;
    justify-content: flex-start;
    color: #253D4E;
    text-transform: capitalize;
    font-size: 15px;
    color: #7e7e7e;
}


header .headerTabs .list-inline-item .account-info .list-item{padding: 4px; text-align: left;}

header .headerTabs .list-inline-item .account-info .list-item .list-icon{margin-right: 4px;}
header .headerTabs .list-inline-item .inline-text{font-size: 14px;margin: 1px 0 0 1px; color: #7e7e7e;}
