/* Scroll to Top Button */
.scroll-to-top {
    position: fixed;
    bottom: 9px;
    right: 22px;
    background-color: #fff;
    color: #0000;
    padding: 3px 9px;
    border-radius: 50%;
    text-align: center;
    font-size: 24px;
    cursor: pointer;
    z-index: 1000;
    border: 1px solid #101217;
}
.scroll-to-top svg {
  fill: #000
}

.scroll-to-top:hover {
  background-color: #1c61e7;
  border: 1px solid #1c61e7;;
}
.scroll-to-top:hover svg{
  fill: #fff;
}


.footerWrapper {
    padding: 20px;
    width: 100%;
}
.footerWrapper .footerBoxes .container-fluid{
  padding: 0 32px;
}
.footerWrapper .footerBoxes .box {
    padding: 16px;
    background: #f4f6fa;
    border-radius: 15px;
}





  


.footerWrapper footer{padding: 48px 0;}
.footerWrapper .footerBoxes .box img:hover{transform: translateY(-5px); transition: all 0.2 ease-in-out;}

.footerWrapper .footerBoxes .box img{width: 40px; margin-right: 10px; }
.footerWrapper .footerBoxes .box h4{font-size: 14px;margin-bottom: 6px;}
.footerWrapper .footerBoxes .box p {font-size: 14px; opacity: 0.5;margin-bottom: 0px;}

.footerWrapper footer .part1 img{width: 154px; height: auto;}

.footerWrapper footer .part1 p{font-size: 14px; color: #7e7e7e;} 

.footerWrapper footer .part2 ul {padding-left: 0;}

.footerWrapper footer .part2 ul li{list-style: none; padding-left: 0; margin-bottom: 3px; line-height: 33px;}

.footerWrapper footer .part2  h4{font-size: 18px;}
.footerWrapper footer .part2 ul li  a{
    text-decoration: none;     
    color: #000;
    font-size: 500;
    font-size: 14px;
    opacity: .8;
    transition: all .2s ease-in-out;
}

.footerWrapper footer .part2 ul li a:hover{    
    color: #24935f !important;
    opacity: 1;
    padding-left: 5px;
    text-decoration: none;
}

footer .lastStrip .part3 ul {
    margin-bottom: 0;
    padding-left: 25px;
}
footer .lastStrip .part3 ul li a {
    align-items: center;
    background: #1c61e7;
    border-radius: 50%;
    display: flex;
    height: 35px;
    justify-content: center;
    transition: all .2s ease-in-out !important;
    width: 35px;
}

footer .lastStrip .part3 h5 {
    font-size: 18px;
    margin-bottom: 0;
}

footer .lastStrip .part3 ul li a svg {
    color: #fff !important;
    font-size: 20px;
}
footer .lastStrip .part3 ul li a:hover {
    background: #333;
}`, "",{"version":3,"sources":["webpack://./src/components/footer/footer.css"],"names":[],"mappings":";AACA,yBAAyB;AACzB;IACI,eAAe;IACf,WAAW;IACX,WAAW;IACX,sBAAsB;IACtB,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,aAAa;IACb,yBAAyB;AAC7B;AACA;EACE;AACF;;AAEA;EACE,yBAAyB;EACzB,yBAAyB;AAC3B;AACA;EACE,UAAU;AACZ;;;AAGA;IACI,aAAa;IACb,WAAW;AACf;AACA;EACE,eAAe;AACjB;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;AACvB;;;;;;;;;AASA,sBAAsB,eAAe,CAAC;AACtC,2CAA2C,2BAA2B,EAAE,+BAA+B,CAAC;;AAExG,qCAAqC,WAAW,EAAE,kBAAkB,EAAE;AACtE,oCAAoC,eAAe,CAAC,kBAAkB,CAAC;AACvE,oCAAoC,eAAe,EAAE,YAAY,CAAC,kBAAkB,CAAC;;AAErF,iCAAiC,YAAY,EAAE,YAAY,CAAC;;AAE5D,+BAA+B,eAAe,EAAE,cAAc,CAAC;;AAE/D,iCAAiC,eAAe,CAAC;;AAEjD,mCAAmC,gBAAgB,EAAE,eAAe,EAAE,kBAAkB,EAAE,iBAAiB,CAAC;;AAE5G,iCAAiC,eAAe,CAAC;AACjD;IACI,qBAAqB;IACrB,WAAW;IACX,cAAc;IACd,eAAe;IACf,WAAW;IACX,+BAA+B;AACnC;;AAEA;IACI,yBAAyB;IACzB,UAAU;IACV,iBAAiB;IACjB,qBAAqB;AACzB;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;AACtB;AACA;IACI,mBAAmB;IACnB,mBAAmB;IACnB,kBAAkB;IAClB,aAAa;IACb,YAAY;IACZ,uBAAuB;IACvB,0CAA0C;IAC1C,WAAW;AACf;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;IACtB,eAAe;AACnB;AACA;IACI,gBAAgB;AACpB","sourcesContent":["\n/* Scroll to Top Button */\n.scroll-to-top {\n    position: fixed;\n    bottom: 9px;\n    right: 22px;\n    background-color: #fff;\n    color: #0000;\n    padding: 3px 9px;\n    border-radius: 50%;\n    text-align: center;\n    font-size: 24px;\n    cursor: pointer;\n    z-index: 1000;\n    border: 1px solid #101217;\n}\n.scroll-to-top svg {\n  fill: #000\n}\n\n.scroll-to-top:hover {\n  background-color: #1c61e7;\n  border: 1px solid #1c61e7;;\n}\n.scroll-to-top:hover svg{\n  fill: #fff;\n}\n\n\n.footerWrapper {\n    padding: 20px;\n    width: 100%;\n}\n.footerWrapper .footerBoxes .container-fluid{\n  padding: 0 32px;\n}\n.footerWrapper .footerBoxes .box {\n    padding: 16px;\n    background: #f4f6fa;\n    border-radius: 15px;\n}\n\n\n\n\n\n  \n\n\n.footerWrapper footer{padding: 48px 0;}\n.footerWrapper 