___CSS_LOADER_EXPORT___.push([module.id, `.homeSlider{padding: 40px 0;}

.homeSlider img{width: 100%;  max-width: 100%; height: auto; border-radius: 30px;}
.homeSlider .home_slider_main{border-radius: 25px; overflow: hidden; height: 400px}
.homeSlider .slick-arrow {
    position: absolute; 
    top: 47%; 
    width: 40px !important; 
    height: 40px !important;;
    border-radius: 50%;
    background: red;
    z-index: 10;
    background: #f2f3f4;
    transition: all 0.2s ease-in-out; 
}
.homeSlider .slick-prev{left: 2%; }
.homeSlider .slick-next{right: 4%; z-index: 10;}
.homeSlider .slick-prev::before{
    content: '\\f104';
    font-family: FontAwesome !important; 
    color: #0000;
    font-size: 30px;
}
.homeSlider .slick-next::before{
    content: '\\f105';
    font-family: FontAwesome !important;
    color: #0000;
    font-size: 30px;
}
.homeSlider .slick-next{right: 4%;}
.homeSlider .slick-arrow::before{color: #000;}
.homeSlider .slick-arrow:hover{background: #1c61e7;}
.homeSlider .slick-arrow:hover::before{color: #fff;}
.slick-dots {bottom: 30px;}
.slick-dots li.slick-active button:before {
    opacity: .75;
    color: #1c61e7;
    width: 20px;
    height: 20p;
    border-radius: 10px;
    font-size: 20px;
}
.slick-dots li button:before {
    font-size: 18px;
}


