.newsLetterSection {
    height: auto;
    width: 100%;
}
.newsLetterSection .container-fluid {
    padding: 0 45px !important;
}
.newsLetterSection .box {
    background-image: linear-gradient(to bottom right, #f1f1f6, #f1f1f6);
    border-radius: 30px;
    height: auto;
    overflow: hidden;
    padding: 60px;
    position: relative;
    width: 100%;
}
.newsLetterSection .box .newsLetterBanner {
    bottom: inherit !important;
    left: 0 !important;
    position: relative !important;
}
.newsLetterSection .box .info {
    width: 55%;
}
.newsLetterSection .box .img {
    bottom: 10px;
    position: absolute;
    right: 10px;
    width: 35%;
    height: auto;  
}
.newsLetterSection .box .img img{
    height: auto;  
}
.newsLetterSection .box .info p {
    font-size: 22px;
    opacity: .8;
}

.newsLetterSection .box .info h2 {
    font-size: 38px;
    font-weight: 600;
    line-height: 50px;
    opacity: .8;
}
.newsLetterSection .box .info {
    width: 55%;
}

.newsLetterBanner{width: 450px; height: 50px; position: absolute; bottom: 100px; left:9%; z-index: 10;}

.newsLetterBanner input{width: 100%; height: 100%; background: #fff; border-radius:60px ; border: 0px; outline: none !important; padding-left: 80px; font-size: 18px; padding-right: 200px;}

.newsLetterBanner  button{padding: 10px 30px !important;background: #1c61e7; border-radius: 50px !important; color: #fff !important; position: absolute; top: 0px; right: 0px; height: 50px !important; font-size: 16px !important; text-transform: capitalize !important; font-weight: 400 !important;}

.newsLetterBanner svg{    position: absolute;
  top: 12px;
  left: 27px;
  z-index: 10;
  font-size: 22px !important;
  transform: rotate(-21deg);
  opacity: 0.5;
}


  
