___CSS_LOADER_EXPORT___.push([module.id, `.tooltip-container {
    position: relative;
    display: inline-block;
    cursor: pointer;
  }
  
  .tooltip {
    position: absolute;
    background-color: #1c61e7;
    color: #fff;
    padding: 8px;
    border-radius: 4px;
    font-size: 12px;
    white-space: nowrap;
    z-index: 10;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, visibility 0.3s;
  }
  
  .tooltip.visible {
    opacity: 1;
    visibility: visible;
  }
  
  .tooltip-top {
    bottom: 150%;
    left: 50%;
    transform: translateX(-50%);
  }
  
  
  
